import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Badge, Form, Select, Tag} from "antd";
import {_getAllCategory} from "../../../../redux/actions/public/category";
const { Option } = Select;
const CategoryFields = (props)=> {
    let { _getAllCategory } = props
    let {data,loading,pagination,params} = props
    const [newParams,setNewParams] = useState({
        ...params,
        limit: 50,
        // is_verify:1,
        // is_published:1
    })
    useEffect(()=> {
        _getAllCategory({
            ...newParams
        })
    },[_getAllCategory])

    const [category,setCategory] = useState([])


    const handleChange = (val, options)=> {
        // item.forEach((object)=>{
        //     delete object['key']
        //     delete object['value']
        //     delete object['children']
        //     delete object['about']
        //     delete object['is_published']
        //     delete object['is_verify']
        //     delete object['date']
        //     object['category'] = object['_id']
        //     delete object['_id']
        //
        // })
        props?.onChangeCategory(options)

        // console.log(props)
        // setCategory(options)
        // props?.form?.setFieldsValue({'category':options})
    }
    return (
        <div className="w-100">
            {
                !loading && (
                    <Form.Item name="category">
                        <Select
                            mode="multiple"
                            className="w-100"
                            onChange={handleChange}
                            placeholder="Category">
                            {typeof(data) !== "undefined" && Array.isArray(data) && data.length > 0 &&
                                data.map(elm => (
                                    <Option key={elm?._id} value={elm?._id} item={elm}>
                                        <Badge color={elm?.background} text={elm?.name}/>

                                    </Option>
                                ))
                            }
                        </Select>
                    </Form.Item>
                )
            }
        </div>
    )
}

CategoryFields.propTypes = {
    _getAllCategory: PropTypes.func.isRequired,
}
CategoryFields.defaultProps = {}

const mapStateToProps = ({auth,Taxonomy})=> {
    const {token} = auth
    const {collections} = Taxonomy
    let { lists } = collections
    return {...lists,token}
}

const mapDispatchToProps = {
    _getAllCategory
}

export default connect(mapStateToProps,mapDispatchToProps)(CategoryFields)
