import React from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Badge, Button, Card, Form, Input, InputNumber, Select, Switch, TimePicker} from "antd";
import {MinusCircleOutlined, PlusOutlined} from "@ant-design/icons";

const PeriodsFields = (props)=> {
    const formItemLayout = {
        labelCol: {
            xs: { span: 24 },
            sm: { span: 4 },
        },
        wrapperCol: {
            xs: { span: 24 },
            sm: { span: 20 },
        },
    };
    const formItemLayoutWithOutLabel = {
        wrapperCol: {
            xs: { span: 24, offset: 0 },
            sm: { span: 20, offset: 4 },
        },
    };
    return (
        <Card title={'Periode'}>
            <div className="w-100">
                <Form.List name="periods">
                    {(fields, { add, remove }) => {
                        console.log(fields)
                        return (
                            <div>
                                {fields.map((field, index) => (
                                    <Form.Item
                                        label={"Day"}
                                        required={false}
                                        key={field.key}
                                    >
                                        <Form.Item
                                            {...field}
                                            validateTrigger={['onChange', 'onBlur']}
                                            // rules={[
                                            //     {
                                            //         required: true,
                                            //         whitespace: true,
                                            //         message: "Please input passenger's name or delete this field.",
                                            //     },
                                            // ]}
                                            name={[field.fieldKey,'day']}
                                        >
                                            <Select defaultValue={'sunday'}>
                                                {['sunday', 'monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday'].map((item,idx)=> (
                                                    <Select.Option value={item} key={item}>{item}</Select.Option>
                                                ))}
                                            </Select>
                                        </Form.Item>

                                        <Form.Item
                                            {...field}
                                            label={"Open Time"}
                                            validateTrigger={['onChange', 'onBlur']}
                                            name={[field.fieldKey,'open_time']}
                                        >
                                            <TimePicker minuteStep={15} secondStep={10} />
                                        </Form.Item>
                                        <Form.Item
                                            {...field}
                                            label={"Close Time"}
                                            validateTrigger={['onChange', 'onBlur']}
                                            name={[field.fieldKey,'close_time']}
                                        >
                                            <TimePicker minuteStep={15} secondStep={10} />
                                        </Form.Item>
                                        <Form.Item
                                            {...field}
                                            label={'Tutup'}
                                            validateTrigger={['onChange', 'onBlur']}
                                            name={[field.fieldKey,'is_closed']}
                                        >
                                            <Switch defaultChecked={false}/>
                                        </Form.Item>


                                        {fields.length > 1 ? (
                                            <MinusCircleOutlined
                                                className="dynamic-delete-button"
                                                onClick={() => {
                                                    remove(field.fieldKey);
                                                }}
                                            />
                                        ) : null}
                                    </Form.Item>
                                ))}
                                <Form.Item>
                                    <Button
                                        type="dashed"
                                        onClick={() => {
                                            add();
                                        }}
                                        style={{ width: '60%' }}
                                    >
                                        <PlusOutlined /> Add field
                                    </Button>
                                </Form.Item>
                            </div>
                        );
                    }}
                </Form.List>
            </div>
        </Card>
    )
}

PeriodsFields.propTypes = {}
PeriodsFields.defaultProps = {}

const mapStateToProps = ({})=> {
    return {}
}

const mapDispatchToProps = {}

export default connect(mapStateToProps,mapDispatchToProps)(PeriodsFields)
