import React, {useState} from 'react'
import {Input, Row, Col, Card, Form, Upload, InputNumber, message, Select, Switch} from 'antd';
import {PlusOutlined} from '@ant-design/icons';
import CategoryFields from "./CategoryFields";
import LocationField from "./LocationField";
import ApiService from "../../../../services/Api.service";


const GeneralField = props => {
    const [rules] = useState({

        name: [
            {
                required: true,
                message: 'Please enter product name',
            }
        ],
        bio: [
            {
                required: true,
                message: "Please enter Bio"
            }
        ],
        description: [
            {
                required: true,
                message: 'Please enter product description',
            }
        ],
        price: [
            {
                required: true,
                message: 'Please enter product price',
            }
        ],
        comparePrice: [],
        taxRate: [
            {
                required: true,
                message: 'Please enter tax rate',
            }
        ],
        cost: [
            {
                required: true,
                message: 'Please enter item cost',
            }
        ]
    })
    const [is_free, setIsFree] = useState(() => {
        if (props?.form.getFieldsValue('is_free')) return props?.form.getFieldsValue('is_free')
        return false
    })

    // const [photos,setPhotos] = useState([])

    const dummyRequest = ({file, onSuccess}) => {
        // setPhotos([...photos,{...file}])
        if (typeof (props?.mode) !== 'undefined' && props?.mode === "UPDATE") {
            const formdata = new FormData();
            formdata.append(`thumbnail[0]`, file?.originFileObj ?? file)


            formdata.append('id', props?._id ?? null)
            new ApiService({
                url: "/api/v1/admin/travel/update/photos",
                body: formdata,

            }).put()
                .then((result) => {
                    if(!result?.error){
                        message.success(result?.message ?? "Successfully!")
                    }else{
                        message.error(result?.message ?? "Failed")
                    }
                })
                .catch((err) => {
                    message.error(err?.message ?? "Failed")
                })
        }
        setTimeout(() => {
            onSuccess("ok");
        }, 0);
    };

    const imageUploadProps = {
        name: 'file',
        multiple: true,
        listType: "picture",
        progress: {
            strokeColor: {
                '0%': '#108ee9',
                '100%': '#87d068',
            },
            strokeWidth: 3,
            format: percent => percent && `${parseFloat(percent.toFixed(2))}%`,
        },
        showUploadList: true,
        customRequest: dummyRequest
        // action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76'
    }

    const beforeUpload = file => {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
        if (!isJpgOrPng) {
            message.error('You can only upload JPG/PNG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            message.error('Image must smaller than 2MB!');
        }
        return isJpgOrPng && isLt2M;
    }

    return (
        <Row gutter={16}>
            <Col xs={24} sm={24} md={17}>
                <Card title="Basic Info">
                    <Form.Item name="name" label="Travel Name" rules={rules.name}>
                        <Input placeholder="Travel Name"/>
                    </Form.Item>
                    <Form.Item name="bio" label="Bio" rules={rules.bio}>
                        <Input placeholder="Bio"/>
                    </Form.Item>
                    <Form.Item name="description" label="Description" rules={rules.description}>
                        <Input.TextArea rows={4}/>
                    </Form.Item>
                </Card>
                <Card title="Pricing">
                    <Form.Item name={'is_free'} initialValue={is_free}>
                        <Switch
                            onChange={() => (setIsFree(!is_free))}
                            checkedChildren={"FREE"}
                            unCheckedChildren={"PAY"}
                            defaultChecked={is_free}
                        />
                    </Form.Item>

                    {
                        !is_free && (
                            <Row gutter={16}>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item name={['price', "regular"]} label="Regular">
                                        <InputNumber
                                            className="w-100"
                                            formatter={value => `Rp ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                            parser={value => value.replace(/\Rp\s?|(,*)/g, '')}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item name={["price", "family"]} label="Family">
                                        <InputNumber
                                            className="w-100"
                                            value={0}
                                            formatter={value => `Rp ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                            parser={value => value.replace(/\Rp\s?|(,*)/g, '')}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item name={["price", "specials"]} label="Price Special in Week end">
                                        <InputNumber
                                            className="w-100"
                                            formatter={value => `Rp ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                            parser={value => value.replace(/\Rp\s?|(,*)/g, '')}
                                        />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item name={["price", "discount"]} label="Discount %">
                                        <InputNumber
                                            className="w-100"
                                            min={0}
                                            max={100}
                                            formatter={value => `${value}%`}
                                            parser={value => value.replace('%', '')}
                                        />
                                    </Form.Item>
                                </Col>
                            </Row>
                        )
                    }

                </Card>
            </Col>
            <Col xs={24} sm={24} md={7}>
                <Card title="Media">
                    <Upload
                        {...imageUploadProps}
                        beforeUpload={beforeUpload}
                        showUploadList={true}
                        onRemove={(file) => props?.handleRemoveChange(file)}
                        onChange={e => props.handleUploadChange(e)}>
                        <div style={{...props.styles}}>
                            <PlusOutlined/>
                            <div style={{marginTop: 8}}>Upload</div>
                        </div>
                    </Upload>
                </Card>
                <Card title="Category">
                    <CategoryFields
                        onChangeCategory={props?.onChangeCategory}
                    />
                </Card>

                <LocationField/>
            </Col>
        </Row>
    )
}

export default GeneralField
